.Characterbook-content{
    padding: 0vh;
    width: calc(100vw - 15.5vw); /* 스크린 너비에서 패딩을 제외한 값을 사용 */
    margin: 0 auto; /* 가운데 정렬을 위해 margin을 auto로 설정 */
    flex: 1; /* 남은 공간을 모두 채우도록 설정 */
    background-color: #FFFFFF; /* 하얀색 배경 */
    align-items: center;
    text-align: center;
    overflow: hidden;
    justify-content: center;
    height: 100vh;
}

.Characterbook-grid {
    padding: 5vw;
    display: grid;
    gap: 20px 20px;
    align-items: center;
    justify-content: center;
    grid-template-columns:  repeat(auto-fill, minmax(250px, 1fr));
    grid-auto-rows:  repeat(auto-fill, minmax(255px, 1fr));
}

.characterBox {
    width: 250px;
    height: 255px;
    margin: 0;
    border-bottom: 2px solid#f2f2f2;
}

.characterPic {
    width: 200px;
    height: 200px;
    max-width: 250px;
    max-height: 250px;
}

.charBoxText {
    font-size: 16px;
    margin: 0;
    padding: 0;
}

.charBoxTitle {
    font-size: 17px;
}

.charBoxText > * {
    margin: 2px;
}

.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.modal-content {
    background-color: #fefefe;
    padding: 20px;
    border-radius: 8px;
    position: relative;
    max-width: 50%; /* 모달 내에서 이미지가 넘치지 않도록 최대 너비 지정 */
    max-height: 70%;
}
  
.close {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    width: 50px; /* 버튼의 너비 */
    height: 50px; /* 버튼의 높이 */
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%; /* 버튼을 둥글게 만들기 위해 */
    background-color: #ffffff; /* 하얀색 배경색 */
    color: #333; /* 버튼 색상 */
}

.modal-character {
    width: 50%; /* 모달 내에서 이미지가 넘치지 않도록 최대 너비 지정 */
    height: 50%; /* 모달 내에서 이미지가 너무 크게 나타나지 않도록 최대 높이 지정 */
    display: block; /* 이미지를 블록 요소로 설정하여 수직으로 정렬되도록 합니다. */
    margin: 0 auto; /* 가로 중앙 정렬 */
  }