.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100vh; /* 화면 전체 높이 */
}

body {
  background-color: #fbf4ec; /*#aaaaaa*/
}

.navigation-bar {
  display: flex;
  flex-direction: row;
  width: 60vh;
  padding: 0vh;
  margin: 0vh;
  justify-content: center;
}

.logo {
  width: 23vh;
  padding: 0vh;
  margin: 0vh;
  /* font-size: calc(10px + 2vmin); */
}

.logo img{
  min-width: 250px;
  width: 20vw;
  padding-top: 10px;
  margin: 0vh;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
}

.widelogo {
  width: 100vw;
  height: 45vh;
  background-color: #ffffff;
  justify-content: center;
  align-content: center;
}

.widelogo img{
  width: 40%;
}

.main-content2 {
  width: 100vw;
  height: 600px;
  background-color: #000000;
  justify-content: center;
  align-content: center;
  color: #ffffff;
}

#text1 {
  font-size: 30px;
}

#text2 {
  font-size: 70px;
}

.main-content2 > * {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  color: #ba67ff; 
}

.profile {
  padding: 3vh;
  margin: 0vh;
  width: 20vh;
  font-size: calc(10px + 2vmin);
}

.nav-bar {
  list-style-type: none;
  /* justify-content: space-between; */
  display: flex;
  flex-direction: row;
  width: 60vh;
  margin: 0;
  padding: 0;
  font-size: calc(10px + 1.5vmin);
}

.nav-bar li {
  flex: 1; /* 동일한 너비를 가지도록 설정 */
  text-align: center; /* 가운데 정렬 */
}

.profile {
  list-style-type: none;
  /* justify-content: space-between; */
  display: flex;
  flex-direction: row;
  width: 10vw;
  margin: 0;
  padding: 0;
  text-decoration: double;
  font-size: calc(10px + 1vmin);
}

.main-content {
  padding: 0vh;
  width: calc(100vw - 30vw); /* 스크린 너비에서 패딩을 제외한 값을 사용 */
  margin: 0 auto; /* 가운데 정렬을 위해 margin을 auto로 설정 */
  flex: 1; /* 남은 공간을 모두 채우도록 설정 */
  background-color: #FFFFFF; /* 하얀색 배경 */
  height: 100vh;
}

.App-header {
  border-bottom: 2px solid #a1239d;
  background-color: #ffffff;
  height: 13vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
  padding: 0vh;
  margin: 0vh;
  box-sizing: border-box;
}

@media (prefers-reduced-motion: no-preference) {
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}