.mymap-content{
    padding: 0vh;
    width: calc(100vw - 15.5vw); /* 스크린 너비에서 패딩을 제외한 값을 사용 */
    margin: 0 auto; /* 가운데 정렬을 위해 margin을 auto로 설정 */
    flex: 1; /* 남은 공간을 모두 채우도록 설정 */
    background-color: #FFFFFF; /* 하얀색 배경 */
    align-items: center;
    justify-content: center;
    text-align: center;
    overflow: hidden;
    height: 100vh;
}

.map-container {
    margin: 2vh 5vw;
    position: relative;
    display: flex;
    flex-direction: row; /* 요소들을 가로로 배치 */
    align-items: center; /* 요소들을 수직 중앙에 배치 */
    width: 100%;
    height: 60vh; /* 지도의 높이 */
}

.map-container > * {
    margin: 0 10px; /* 각 요소 좌우에 10px의 여백 추가 */
}

#map {
    margin-left: 20px;
}

.button1 {
    width: 135px;
    height: 45px;
    background-color: #2f83ef;
    color: #fff;
    padding: 8px 15px;
    border: none;
    border-radius: 8px;
    font-size: 17px;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.button1:hover {
    background-color: #f7c548; /* 마우스 호버 시 배경색 변화 */
    color: #000;
}

.button2 {
    width: 140px;
    height: 50px;
    background-color: #fbf8f1;
    color: #a1239d;
    padding: 8px 15px;
    border: 3px solid #b536b1;
    border-radius: 15px;
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
}

.button2:active {
    background-color: #f7c548; /* 마우스 호버 시 배경색 변화 */
}

.headline {
    width: 100%;
    height: 10vh;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center; /* 요소들을 수직 중앙에 배치 */
    background-color: #fdf9ff;
    flex-direction: column;
}

.headline > * {
    margin: 0;
}

.buttonsMM {
    margin-left: calc(100vw*0.01);
    margin-right: 10px;
    width: 150px;
    height: 60vh;
    display: block;
    align-items: center; /* 요소들을 수직 중앙에 배치 */
}

.museumInfo {
    width: 200px;
    height: 100px;
    background-color: #FFFFFF;
    color: #000000;
    overflow: hidden;
}

.museumInfoTitle {
    background-color: #ebebeb;
    width: 200px;
    height: auto;
    border-bottom: #727272;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3px 10px;
    word-break: keep-all;
    overflow-wrap: break-word; /* 긴 단어를 여러 줄로 줄바꿈할 수 있도록 설정 */
    white-space: normal;
    align-self: stretch;
    text-align: left;
}